@import "../../../variables";

#plugin-gnosis-conainer {
  height: 100%;

  iframe {
    height: 100%;
    width: 100%;
    overflow: auto;
    box-sizing: border-box;
    border: 0;
  }

  #dapps-container {
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    margin: 0px -16px 0px -16px;

    li {
      margin: 0px 16px;
      flex: 1 0 120px;
      list-style: none;
      background: $panel-background-color;
      padding: 16px;
      border-radius: 8px;
      cursor: pointer;

      &:hover {
        background: lighten($panel-background-color, 1%);
      }

      div.logo-container {
        margin: 12px auto 8px auto;
        width: 48px;
        height: 48px;
        border-radius: 4px;
        background-size: contain;
        background-repeat: no-repeat;
      }

      div.dapp-name {
        text-align: center;
        font-size: 2em;
        padding-bottom: 16px;
      }

      div.dapp-desc {
        min-width: 0;
        overflow: hidden;
        text-align: center;
        text-overflow-ellipsis: "...";
        font-size: 0.9em;
        line-height: 1.4em;
        color: $text-container-color;
      }
    }
  }
}
